<template>
    <div class="wrapper" ref="wrapper">
        <div ref="banner">
            <slot name="banner"></slot>
        </div>
        <div class="scroll">
            <div ref="stickyNav" class="scroll__graphic">
                <ul class="sticky-nav" :style="{color: activeColor}" v-show="navList.length > 0">
                    <li class="yd-tab-nav-item"
                        v-for="item, key in navList"
                        :key="key"
                        :class="item._uid == activeIndex ? 'yd-tab-active' : ''"
                        @click="changeHandler(item._uid, item.label, item.tabkey,item.panel,key)">
                        <a href="javascript:;">{{item.label}}</a>
                    </li>
                </ul>
            </div>
            <div class="yd-sticky-panel" ref="panel">
                <slot></slot>
            </div>
        </div>
        <div class="yd-lyout-footer">
            <slot name="footer"></slot>
        </div>

    </div>
</template>
<script>
    import 'intersection-observer';
    import 'stickyfilljs'
    import scrollama from 'scrollama';
    import {isColor} from '../../../utils/assist';
    import jump from 'jump.js'
    export default {
        name: "yd-sticky-nav",
        data() {
            return {
                currentKey: '',
                navList: [],
                activeIndex: 0,
                tmpIndex: 0,
                tabPanels: []
            };
        },
        props: {
            change: Function,
            callback: Function,
            activeColor: {
                validator(value) {
                    if (!value) return true;
                    return isColor(value);
                },
                default: '#005bac'
            },
            offset: {
                type: Number,
                default: 0
            }
        },
        methods: {
            emitChange(label, tabkey) {
                this.callback && this.callback(label, tabkey);
            },
            changeHandler(uid, label, tabkey, panel, index) {
                if (this.tmpIndex != uid) {
                    this.activeIndex = this.tmpIndex = uid;
                    this.emitChange(label, tabkey);
                }
                jump(panel.$el, {offset: -this.navOffSet, duration: 400})
            },

            handleStepEnter(response) {
                this.activeIndex = this.navList[response.index]._uid;
            },
            init() {
                const topHeigth = 0;//this.$refs.banner.offsetHeight+this.$refs.stickyNav.offsetHeight
                const tabPanels = this.$children.filter(item => item.$options.name === 'yd-sticky-panel');
                this.navList = [];
                const panelList = [];
                tabPanels.forEach((panel, index) => {
                    this.navList.push({
                        _uid: panel._uid,
                        label: panel.label,
                        tabkey: panel.tabkey,
                        panel: panel
                    });
                    panelList.push(panel.$el)
                });
                if (!this.scroller) {
                    this.scroller = scrollama();
                    this.scroller.setup({
                        container: '.scroll', // required (for sticky)
                        graphic: '.scroll__graphic', // required (for sticky)
                        text: '.scroll__text',
                        step: panelList,
                        offset: 0.33
                    })
                            .onStepEnter(this.handleStepEnter)
                    this.$nextTick(() => {
                        window.addEventListener('resize', this.resizeHandler);
                        this.navOffSet = this.$refs['stickyNav'].offsetHeight + this.offset
                    })
                }

            },
            resizeHandler() {
                console.log("")
                this.scroller.resize()
            }
        },
        mounted() {

        },
        created() {
            this.inited = false;
            this.scroller;
            this.navOffSet = this.offset;
        }
    };

</script>
<style lang="less" scoped>
    @import "../../../styles/components/tab.less";
    .scroll {
        position: relative;
    }

    .scroll__graphic {
        position: -webkit-sticky;
        position: sticky;
        z-index: 100;
        top: 0;
        -webkit-transform: translate3d(0, 0, 0);
        -moz-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }

    .yd-sticky-panel {
        position: relative;
    }
    .sticky-nav {
        display: flex;
        box-shadow: inset 2px 14px #888;
        -webkit-box-shadow:0 2px 14px #888;
        -moz-box-shadow:0 2px 14px #888;
        .yd-tab-active{
            color: currentColor;
            background-color: #FFF;
            :before{
                content: '';
                width: 70%;
                height: 2px;
                position: absolute;
                left: 50%;
                bottom: 0;
                margin-left: -35%;
                z-index: 4;
                background-color: currentColor;
            }
        }
        .yd-tab-nav-item {
            font-size: 16px;
            line-height: 42px;
            flex: 1;
            &:not(:last-child):after{
                display: none;
            }
        }
        ul::after{
            content: '';
            width: 100%;
            height: 2px;
            position: absolute;
            left: 0;
            bottom: 0;
            z-index: 4;
            background-color: currentColor;
        }
    }
    .yd-lyout-footer{
        position: fixed;
        bottom: 0;
    }
</style>



